*{
    margin:0;
    padding:0;
}
.signInParent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height:100vh;
    width:100vw;
}

.Site {
    width:100vw;
    height:100vh;
    background-color: rgb(33, 33, 33);
}

.lightBG{
    background-color: #fdffff;
}
.logo{
    width:6cm;
    height:auto;
}

.btnDiv{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 0.2cm;
}
.signInBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1cm;
    row-gap: 0.3cm;
    background-color:  rgb(39, 39, 39);
    width: fit-content;
    border-radius: 12px;
    width:10cm;
}

.inputEmail, .inputPassword{
    padding:0.3cm;
    align-items: center;
    width:7cm;
    border-radius: 6px;
    border:thin solid rgb(102, 102, 102);
    background-color:transparent;
    color:rgb(255, 255, 255);
    font-weight: bolder;
}

.inputEmail:focus, .inputPassword:focus{
    outline:3px solid rgb(0, 123, 255);
}

.inputSelect{
    padding:0.3cm;
    align-items: center;
    width:7.7cm;
    border-radius: 6px;
    border:thin solid rgb(102, 102, 102);
    background-color:transparent;
    color:rgb(244, 244, 244);
    font-weight: bolder;
}

.optionSelect{
    border:thin solid rgb(102, 102, 102);
    background-color: rgb(198, 143, 250);
    color:black;
}

input::placeholder {
    color: #f5f5f5;
    font-weight:100;
}

.labelCheckBox{
    padding:5px;
}
.labelText{
    color:rgb(255, 50, 50);
    font-size: 14px;
}
.signInHeading{
    font-size: 20px;
    font-weight: bold;
    color: #fdffff;
}

.createAccount{
    font-size: 14px;
    color:rgb(91, 203, 255);
    cursor: pointer;
    text-decoration: none;
}

.btn{
    border:3px solid rgb(0, 136, 255);
    color:rgb(255, 255, 255);
    background-color: transparent;
    border-radius:6px;
    cursor:pointer;
    padding:0.2cm 0.3cm;
}

.Dark{
    color:rgb(238, 238, 238);
}

.btn-disabled{
    border:3px solid rgb(105, 105, 105);
    color:rgb(255, 255, 255);
    background-color: transparent;
    border-radius:6px;
    padding:0.2cm 0.3cm;
    cursor: not-allowed;  
  pointer-events: none;
}

.btn:hover{
    /* transition: 1s ease; */
    background-color:rgb(0, 136, 255);
    color:rgb(255, 255, 255);
    border-radius:6px;
    cursor:pointer;
}

#overlayDiv {
    display: none;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: 1;
    position: absolute;
  }

.content-Home{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Booklist{
    width:95%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* padding: 1cm; */
}
.bookTable{
    justify-content: center;
    align-items: center;
    border:thin solid grey;
    width:100%;
}

.bookCell{
    border:thin solid rgb(174, 174, 174);
}
